<template>
    <div class="sub-wrapper">
        <scroller>
            <div class="delete">
                <div class="slider">
                    <div class="content" id="1"
                         @touchstart='touchStart'
                         @touchmove='touchMove'
                         @touchend='touchEnd'
                         @click="Totalk"
                         :style="this.target==1?deleteSlider:' '"
                    >
                        <div class="num-msg">
                            <img src="http://img1.imgtn.bdimg.com/it/u=3532267716,2233133573&fm=27&gp=0.jpg">
                            <span class="dot">
                                <span class="num">11</span>
                            </span>
                        </div>
                        <div class="center">
                            <p class="name"></p>
                            <span class="msg"></span>
                            <span class="time">2018.1.16</span>
                        </div>
                    </div>
                    <div class="remove" ref='remove' @click="remove">
                        
                    </div>
                </div>
                <div class="slider slider2">
                    <div class="content"  id="2"
                        :style="this.target==2?deleteSlider:' '"
                         @touchstart.stop='touchStart'
                         @touchmove.stop='touchMove'
                         @touchend.stop='touchEnd'
                         @click="Totalk"
                    >
                        <div class="num-msg">
                            <img src="http://img1.imgtn.bdimg.com/it/u=3532267716,2233133573&fm=27&gp=0.jpg">
                            <span class="dot">
                        <span class="num">11</span>
                    </span>
                        </div>
                        <div class="center">
                            <p class="name"></p>
                            <span class="msg"></span>
                            <span class="time">2018.1.16</span>
                        </div>
                    </div>
                    <div class="remove" ref='remove' @click="remove">
                        
                    </div>
                </div>
            </div>
        </scroller>
    </div>

</template>

<script type="text/ecmascript-6">
    /*import {API_FRIEND_LIST} from 'common/js/api';*/
    import qs from 'qs';
    import axios from 'axios';
    import {mapMutations} from 'vuex';
    import {API_MESSAGE} from "common/js/api";

    export default {
        data() {
            return {
                startX:0,   //
                endX:0,     //
                moveX: 0,   //
                disX: 0,    //
                deleteSlider: '',//,v-bind:style="deleteSlider"
                items:[],
                target:'',
                tarDel:'',
                websock: null,
            }
        },
        activated(){
            // this._request();
        },
        created: function () {
            this.isLogin = this.$login.isLogin();
            this._request()
            // webcockt
            this.initWebSocket()
            // let token = this.$login.getToken();
            // this._request(token);
        },
        activated() {
            this.isLogin = this.$login.isLogin();
        },
        components:{
        },
        methods:{
            /*async _request(){
                axios.get('/src/page/message/subpage/message/text.json')
                    .then(function (response) {
                        console.log(response.data.num1);
                        var items=response.data.num1;
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
            },*/
            
            async _request(){
                // setInterval(function(){
                //     console.log(1)
                // },1000)
                let res = await axios.post(API_MESSAGE,qs.stringify({
                    utoken:this.$login.getToken(),
                    
                }));
                let data = res.data;
                console.log(data);
                console.log();
            },
            touchStart(ev){
                ev= ev || event;
                //tounches1
                if(ev.touches.length == 1){
                    // 
                    this.startX = ev.touches[0].clientX;
                }
            },
            touchMove(ev){
                ev = ev || event;
                //
                this.target=ev.currentTarget.id;
                let wd=this.$refs.remove.offsetWidth;
                if(ev.touches.length == 1) {
                    // 
                    this.moveX = ev.touches[0].clientX;
                    // 
                    this.disX = this.startX - this.moveX;
                    // console.log(this.disX);
                    // 
                    if(this.disX < 0 || this.disX == 0) {
                        this.deleteSlider = "transform:translateX(0px)";
                        this.target=false;
                        // 0
                    }else if (this.disX > 0) {
                        // *5
                        this.deleteSlider = "transform:translateX(-" + this.disX*5 + "px)";
                        // 
                        if (this.disX*5 >=wd) {
                            this.deleteSlider = "transform:translateX(-" +wd+ "px)";
                        }
                    }
                }
            },
            touchEnd(ev){
                ev = ev || event;
                let wd=this.$refs.remove.offsetWidth;
                if (ev.changedTouches.length == 1) {
                    let endX = ev.changedTouches[0].clientX;
                    this.disX = this.startX - endX;
                    // console.log(this.disX)
                    //,
                    if ((this.disX*5) < (wd/2)) {
                        this.deleteSlider = "transform:translateX(0px)";
                    }else{
                        // 
                        this.deleteSlider = "transform:translateX(-"+wd+ "px)";
                    }
                }
            },
            remove(ev){
                this.tarDel=ev.currentTarget;
                let del=this.tarDel.parentNode;
                del.style.display='none';
            },
            Totalk(){
                this.$router.push('/message/talk');
            },
            //
            /*async _request(token) {
                let res = await axios.post(API_FRIEND_LIST, qs.stringify({
                    utoken: token,
                    targetType: 'users'
                }));
                let data = res.data;
                // console.log(data);
                this.friendList = data.list;
                this.setFriendList(data.list)
            },
            ...mapMutations({
                'setFriendList': "SET_FRIEND_LIST"
            })*/
            // websocket
            threadPoxi(){  // 
                //
                const agentData = "mymessage";
                //ws
                if (this.websock.readyState === this.websock.OPEN) {
                    this.websocketsend(agentData)
                }
                //  300
                else if (this.websock.readyState === this.websock.CONNECTING) {
                    let that = this;//this
                    setTimeout(function () {
                        that.websocketsend(agentData)
                    }, 300);
                }
                //  500
                else {
                    this.initWebSocket();
                    let that = this;//this
                    setTimeout(function () {
                        that.websocketsend(agentData)
                    }, 500);
                }
            },
            initWebSocket(){ //weosocket
                //ws
                const wsuri = process.env.WS_API + "/websocket/threadsocket";
                this.websock = new WebSocket(wsuri);
                this.websock.onmessage = this.websocketonmessage;
                this.websock.onclose = this.websocketclose;
            },
            websocketonmessage(e){ //
                const redata = JSON.parse(e.data);
                console.log(redata.value);
            },
            websocketsend(agentData){//
                this.websock.send(agentData);
            },
            websocketclose(e){  //
                console.log("connection closed (" + e.code + ")");
            }
        },
    }
</script>

<style rel="stylesheet/stylus" lang="stylus" scoped>
    @import "~common/stylus/variable"

    .sub-wrapper
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        top: 90px;
        background-color: $color-bg

    .slider{
        width: 100%;
        height:66px
        position: relative;
        user-select: none;
        .content{
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background:#FFFFFF;
            z-index: 100;
            //    
            transition: 0.3s;

            width 100%
            border-bottom 1px solid #CCCCCC
            background-color #FFFFFF
            padding 8px 5px
            display flex
            align-items center
        }
            .num-msg{
                position relative
            }
                .dot{
                    position absolute
                    top:-5px
                    left 41px
                    width 18px
                    height 18px
                    background-color red
                    border-radius 50%
                    display flex
                    align-items center
                    justify-content center
                }
                    .num{
                        color #FFFFFF
                    }
            img {
                width 50px
                height 50px
                border-radius 5px
            }
            .center{
                margin-left 5px
                position relative
                width 100%
            }

                .name{
                    font-size 16px
                    color #000000
                }
                .msg{
                    ext-overflow: -o-ellipsis-lastline;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    color #999999
                }
                .time{
                    position absolute
                    top 0
                    right 0
                    color #999999
                }


        .remove{
            position: absolute;
            width:100px;
            height:66px;
            background:red;
            right: 0;
            top: 0;
            color:#fff;
            text-align: center;
            font-size: 20px;
            line-height: 66px;
        }
    }

</style>



