<template>
    <div class="bill-detail-wrapper">
        <v-header path="1" :goback="true" title=""></v-header>
        <div class="bill-detail-msg">
            <div></div>
            <div>2017-10-01</div>
            <div>-100.00</div>
        </div>
        <div class="bx">
            <mt-cell title="" is-link>
                <span></span>
            </mt-cell>
            <mt-cell title="" is-link>
                <span></span>
            </mt-cell>
            <mt-field label="" placeholder="" v-model="goodsname"></mt-field>
            <mt-cell title="" is-link>
                <span></span>
            </mt-cell>
            <img-upload style="margin-top: 5px;" title=""></img-upload>
        </div>
        <div class="bill-detail-btn"></div>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';
    import ImgUpload from 'components/img-upload/img-upload';

	export default {
	    data() {
	        return {
                goodsname: ''
            }
        },
	    components: {
	        VHeader,
            ImgUpload
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    .bill-detail-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 1000
        background-color: $color-bg
        .bill-detail-msg
            height: 150px
            color #fff
            background-color: $color-black
        .bx
            width: 100%
            margin-top: 5px
        .bill-detail-btn
            position: absolute
            bottom: 0
            left: 0
            right: 0
            height: 42px
            line-height: 42px
            text-align: center
            color #fff
            background-color: $color-orange
        .mint-cell-text
            font-size: 12px
            color #8e8d8d
</style>



