<template>
    <div class="approve-detail-wrapper">
        <v-header :path="fromUrl" title=""></v-header>
        <scroller style="margin-top: 50px">
        <div class="approve-detail-main">
            <div class="title border-1px">
                <img src="//fuss10.elemecdn.com/f/8d/f29dbf20be425fc12426c0b1f90b7jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/" alt="">
                <div class="who"></div>
                <div class="state"></div>
            </div>
            <div class="table-item">
                <div class="table-name"></div>
                <div class="table-value"></div>
            </div>
            <div class="table-item">
                <div class="table-name"></div>
                <div class="table-value"></div>
            </div>
            <div class="table-item">
                <div class="table-name"></div>
                <div class="table-value">2017-12-25</div>
            </div>
            <div class="table-item">
                <div class="table-name"></div>
                <div class="table-value">2017-12-26</div>
            </div>
            <div class="table-item">
                <div class="table-name"></div>
                <div class="table-value"></div>
            </div>
        </div>
        <div class="timeline">
            <Timeline>
                <TimelineItem color="green">
                    <p></p>
                    <p>2017-12-25</p>
                </TimelineItem>
                <TimelineItem color="green">
                    <p></p>
                </TimelineItem>
            </Timeline>
        </div>
        </scroller>
        <ButtonGroup style="width: 100%;position: absolute;left: 0;bottom: 0;">
            <Button type="error" long style="width: 50%;border-radius: 0"></Button>
            <Button type="success" long style="width: 50%;border-radius: 0"></Button>
        </ButtonGroup>
    </div>
</template>

<script>
    import VHeader from 'components/header/header';

	export default {
	    data() {
	        return {
	            fromUrl: ''
            }
        },
        beforeRouteEnter  (to, from, next) {
            next( vm => {
                vm.fromUrl = from.path;
            })
        },
	    components: {
	        VHeader
        }
    }
</script>

<style rel="stylesheet/stylus" lang="stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"

    .approve-detail-wrapper
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        z-index: 999999999999999
        background-color: $color-bg
        .approve-detail-main
            padding: 10px
            background-color: #fff
            .title
                margin-bottom: 10px
                position: relative
                padding-left: 40px
                padding-bottom: 10px
                border-b(#ccc)
                img
                    position: absolute
                    left: 0
                    top: 0
                    width: 32px
                    height: 32px
                    border-radius 2px
                .who
                    line-height: 18px
                    font-size: 16px
                .state
                    color $color-orange
            .table-item
                display: flex
                .table-name
                    color #999
                    flex 0 0 70px
                .table-item
                    flex 1
        .timeline
            margin-top: 10px
            padding: 10px 10px 0
            background-color: #fff
</style>



